
Template.__checkName("MeteorToys_status");
Template["MeteorToys_status"] = new Template("Template.MeteorToys_status", (function() {
  var view = this;
  return HTML.DIV({
    "class": "MeteorToys_orb MeteorToys_button",
    id: "MeteorToys_status",
    oncontextmenu: "return false;"
  }, "	\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("online"));
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "MeteorToys_icon MeteorToys_icon_online"
    }), "\n		" ];
  }), "\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("connecting"));
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "MeteorToys_icon MeteorToys_icon_connecting"
    }), "\n		" ];
  }), "\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("offline"));
  }, function() {
    return [ "\n			", HTML.DIV({
      "class": "MeteorToys_icon MeteorToys_icon_offline"
    }), "\n		" ];
  }), HTML.Raw('\n		<div class="MeteorToys_orb_filler_wrapper"></div>\n	'));
}));
